@import "../_tuan-mixins.less";
@import "../_tuan-variables.less";

.fn-brand-group{
	.brand-item{
		background: #fff;
		margin-top: 2%;

		&.slim-content{
			margin-top: unit(@tuan-slim-mt, %);
		}

		&:first-child{
			margin-top: 0;
		}

		.pic{
			display: block;

			&:before{
				padding-top: 40%;
			}

			.countdown{
				.absolute(5%,0,_,_);
				background: rgba(0,0,0,0.5);
				border-top-left-radius: 12*@rex;
				border-bottom-left-radius: 12*@rex;
				color:#fff;
				padding: 2*@rex 8*@rex;
			}
		}

		.info{
			padding: 2%;
			display: table;
			width: 100%;

			.logo{
				display: table-cell;
				width: 26.8%;

				&:before{
					padding-top: 39.2%;
				}
			}

			.desc{
				display: table-cell;
				vertical-align: middle;
				color: @ht-color;
				.ellipsis();
				width: 36%;
				text-align: center;
			}

			.num{
				display: table-cell;
				vertical-align: middle;
				width: 27.2%;
				color: #999;
				text-align: right;
				.ellipsis();
			}
		}
	}
}
