@import "../_tuan-mixins.less";
@import "../_tuan-variables.less";

.fn-proline-list{
	.item{
		padding-top: 2%;
		background: #fff;
		width: 100%;
		display: table;

		&:last-child .info{
			border-bottom: none;
		}

		.pic{
			display: table-cell;
			vertical-align: middle;
			width: 31.5%;

			span{
				display: block;
			}
		}

		.info{
			display: table-cell;
			vertical-align: top;
			padding: 2% 15*@rex;
			border-bottom: 1px solid @bd-color;

			.title{
				display: block;
				line-height: 18*@rex;
				height: 36*@rex;
				overflow: hidden;
			}

			.price{
				.now{
					max-width: 35%;
				}

				.primary{
					max-width: 28%;
				}

				.goods-preparing{
					max-width: 32%;
					margin-right: 0;
				}


			}
		}
	}
}
