@import "../_tuan-mixins";
@import "../_tuan-variables";

.item-v-list{
	.item{
		width:49.3%;
		float: left;
		background: #fff;
		margin-top: 1.4%;
		overflow: hidden;
		position: relative;


		&-wrap{
			.absolute(0,_,_,0);
			.size(100%);
		}

		&:nth-child(even){
			float: right;
		}

		.img-box{
			display: block;
		}

		.title{
			display: block;
			color: #333;
			height:unit(@line-height-base * @price-size-num-md * 2,rem);
			.ellipsis(2);
			width: 90%;
			margin:10*@rex auto;
			overflow: hidden;
		}

		.price{
			padding-left: 10*@rex;
		}

		&-img{
			.item-wrap{
				padding-top:50%;
				padding-bottom:97*@rex;

				.img100{
					.absolute(0,_,_,0);
					.size(100%);
				}
			}
		}

	}
}
