@import "../_tuan-mixins.less";
@import "../_tuan-variables.less";

.fn-bottom-nav{
	.fixed(_,0,0,0);
	width: 100%;
	max-width: @container-max-width;
	z-index: 200;
	transform: translateZ(0);
	background: #fff;
	border-top: 1px solid @bd-color;
	margin: 0 auto;

	.item{
		width: 20%;
		float: left;
		text-align: center;
		padding: 3px 0;

		em{
			font-size: 20*@rex;
			display: block;
		}

		span{
			font-size: 12*@rex;
			display: block;
		}

		&-active{
			color: @ht-color;
		}
	}

	.item-list-2{
		.item{
			width: 50%;
		}
	}

	.item-list-3{
		.item{
			width: 33.3%;
		}
	}

	.item-list-4{
		.item{
			width: 25%;
		}
	}
}
