@import "../common/mixins.less";

.price{
				padding-bottom:10*@rex;
				overflow: hidden;

				.now,.primary{
					max-width: 42%;
					overflow: hidden;
					display: inline-block;
					vertical-align: bottom;
					line-height: 1;
				}

				.primary{
					color: #969696;
					text-decoration: line-through;
					max-width: 42%;
				}

				.vip{
					min-width: 20%;
					height: 20*@rex;
					display: block;
				}

				.vip-txt{
					line-height: 1;
					display: inline-block;
					vertical-align: bottom;
					color: #E71B2B;
					border: 1*@rex solid #E71B2B;
					padding: 1*@rex 2*@rex;
					border-radius: 3*@rex;
					font-size: 12*@rex;
				}
				.goods-preparing{
					max-width: 40%;
					overflow: hidden;
					white-space: nowrap;
					line-height: 1;
					float: right;
					margin-right: 10*@rex;
					font-size: 13*@rex;
					color: #999;
					position: relative;
					top: 7*@rex;
				}
			}

@media screen and(max-width:320px){
	  .price{
 		.goods-preparing{
 			font-size: 10*@rex;
 			top: 8*@rex;
 		}

	  }
}
