@import "../_tuan-mixins.less";
@import "../_tuan-variables.less";

.fn-pic-item3{
	.line{
		position: relative;
		border-bottom: 1px solid @bg-color;

		&:last-child{
			border-bottom: none;
		}

		&:before{
			content: '';
			display: block;
			padding-top: 32%;
		}

		&-inner{
			.absolute(0,_,_,0);
			.size(100%);
		}
	}

	.item{
		float: left;
		position: relative;
		.size(33.33%, 100%);

		&:after{
			.absolute(0,_,_,0);
			content: '';
			.size(1px, 100%);
			background: @bg-color;
		}

		&:first-child{
			width: 33.34%;

			&:after{
				display: none;
			}
		}
	}
}
