@import "../_plugin/fixed-category.less";
@import "../_tuan-mixins.less";
@import "../_tuan-variables.less";

.fn-tabs-nav{
	.tab-blue{
		.sub-list li.active a{
			color:#2092fc;
		}

		.item-list{
			background: #2092fc;

			.navs {
				.list a{
					color: #fff;

					&:before{
						content: none;
					}

					&.active{
						color:#fff;
						background:#0e75d4;
					}
				}

				.type{
					background: #2092fc;
					color:#fff;
				}
			}

			.toggle-btn{
				box-shadow:-2*@rex 0*@rex 4*@rex #1c7fda;

				&:before,&:after{
					background-color: #fff;
				}
			}
		}
	}
}
