@import "../_tuan-mixins.less";
@import "../_tuan-variables.less";

.fn-loading-msg{
	margin-bottom: 2%;

	.line{
		position: relative;
		text-align: center;

		&:before{
			content: '';
			.size(100%, 1px);
			.absolute(50%,_,_,0);
			background: @bd-color;
		}

		span{
			display: inline-block;
			position: relative;
			vertical-align: top;
			padding: 0 15*@rex;
			color: #999;
			background: @bg-color;
		}
	}
}
