@import "../_tuan-mixins.less";
@import "../_tuan-variables.less";

.fn-product-vs{
	.pro-vs{
		background-size: 100% 100%;
		background-repeat: no-repeat;
		background-position: top center;
	}

	.pro-content{
		padding: 11.33% 0 3.2% 0;

		&-inner{
			width: 93.34%;
			margin: 0 auto;
		}
	}

	.tab-list{
		overflow: hidden;

		.tab{
			float: left;
			text-align: center;
			padding-left: 1px;
			width: 50%;

			&:first-child{
				padding-left: 0;
			}

			> a{
				display: block;

				&:before{
					padding-top: 19.8%;
				}

				.active-img{
					display: none;
				}
			}

			&.active > a{
				.active-img{
					display: block;
				}
			}
		}

		&-3{
			.tab{
				width: 33.33%;

				&:first-child{
					width: 33.34%;
				}
			}
		}

		&-4{
			.tab{
				width: 25%;
			}
		}
	}

	.panel{
		background-size: 100% 100%;
		background-repeat: no-repeat;
		background-position: top center;
		overflow: hidden;
		position: relative;

		.bar{
			.size(97%, 18*@rex);
			line-height: 16*@rex;
			.absolute(2.4%,_,_,50%);
			overflow: hidden;
			transform: translateX(-50%);
			border: 1px solid #fff;
			color: #fff;

			.line{
				float: left;
				height: 100%;
				text-align: center;
				overflow: hidden;
			}

			.line-left{
				border-right-width: 1px;
				border-right-style: solid;
				border-right-color: inherit;
			}
		}

		.pic{
			width: 50%;
			float: left;
			position: relative;

			&:before{
				padding-top: 108.56%;
				content: '';
				display: block;
			}

			.img{
				.size(100%);
				.absolute(0,_,_,0);
			}

			.victory{
				.absolute(11%,_,_,_);
				width: 17.37%;
				z-index: 2;

				&:before{
					padding-top: 100%;
					content: '';
					display: block;
				}


				img{
					.absolute(0,_,_,0);
					.size(100%);
				}
			}

			&-left{
				.victory{
					left: 4%;
				}
			}

			&-right{
				.victory{
					right: 4%;
				}
			}
		}
	}
}
