@import "../_tuan-mixins";
@import "../_tuan-variables";

.ui-fixed-category{
	@z-index: @zindex-fixed;
	@nav-size: 44;

	line-height: unit(@nav-size/25, rem);
	height: unit(@nav-size/25, rem);

	.bg{
		.fixed(0,0,0,0);
		opacity: 0;
		z-index: -1;
		transition: opacity .3s ease;
		transform: translateZ(0);
		background-color: rgba(0,0,0,.6);
	}

	.item-list{
		z-index: @z-index;
		max-width: @container-max-width;
		top: 0;
		right: 0;
		left: 0;
		height: unit(@nav-size/25, rem);
		background: #fff;
		margin: 0 auto;
		transform: translateZ(0);

		.navs{
			margin-right: unit(@nav-size/25, rem);
			height: 100%;
			position: relative;

			.list{
				a{
					padding: 0 10*@rex;
					display: block;
					width: auto;
					position: relative;

					&:before{
						content: '';
						.absolute(_,_,0,0);
						.size(100%, 2*@rex);
						background: @ht-color;
						display: none;
					}

					&.active{
						color: @ht-color;

						&:before{
							display: block;
						}
					}
				}
			}

			.type{
				padding: 0 10*@rex;
				display: none;
				background: #fff;
				.absolute(0,_,_,0);
				width: 100%;
			}
		}

		.toggle-btn{
			float: right;
			.size(unit(@nav-size/25, rem), 100%);
			position: relative;
			transition: transform .3s ease;

			&:before,&:after{
				content: '';
				.absolute(50%,_,_,_);
				.size(30%, 5%);
				background-color: #6c6c6c;
				transform-origin: 50% 50%;
			}

			&:before{
				left: 26%;
				transform: rotate(45deg);
			}

			&:after{
				right: 26%;
				transform: rotate(-45deg);
			}
		}
	}

	.sub-list{
		transition: transform .3s ease;
		transform: translateY(-100%);
		z-index: -1;
		.fixed(0, 0, _, 0);
		max-width: @container-max-width;
		margin: 0 auto;
		background: rgba(255,255,255,0.9);
		min-height: unit(@nav-size/25, rem);

		li{
			float: left;
			width: 25%;
			text-align: center;
			padding: 0 10*@rex;

			&.active a{
				color: @ht-color;
			}
		}
	}

	&.show,&.active{
		.item-list{
			position: fixed;
		}
	}

	&.show{
		.item-list .toggle-btn{
			transform: rotate(180deg);
		}

		.bg{
			z-index: @z-index;
			opacity: .7;
		}

		.sub-list{
			z-index: @z-index;
			transform: translateY(0);
			.fixed(unit(@nav-size/25, rem), 0, _, 0);
		}

		.item-list{
			.navs{
				.list{
					visibility: hidden;
				}

				.type{
					display: block;
				}
			}
		}
	}
}
